$--bgh:#3d55df;
$--bgb:#324cdd;
$--bg:#5e72e4;
$--bb:#5e72e4;
$mobile-width:200px;
$tablet-width: 768px;
$desktop-width: 1026px;
@mixin mobile {
    @media (min-width: #{$mobile-width}) and (max-width: #{$tablet-width - 1px}) {
      @content;
    }
  }
  
@mixin tablet {
    @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
      @content;
    }
  }
@mixin sizes($--wi){
    .texth{
        margin-top: 5px;
        margin-left: 33px;
        height: 40px;
        width: $--wi;
        background-color: $--bg;
        text-align: center;
        font-size: .875rem;
        border:1px solid $--bb;
        border-radius: .25rem;
        text-transform: uppercase;
        color: #fff;
        cursor: pointer;
        box-shadow: 0 4px 6px rgba(50,50,93,.11);
    }
    .texth:hover{
        background-color: $--bgh;
        border:1px solid $--bgb;
        box-shadow: 0 7px 14px rgba(50,50,93,.1);
        transform: translateY(-1px);
    }
    
}

//small size
.sizes-Small-container{
    // transform: opacity .15 linear !important;
    // opacity: 0;
    transform: translateY(-700px);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0 ,0,0,.2);
    z-index: 10; 

    &-content-slide{
     transform: translateY(50px);
     transition: transform .3s ease-out;
    }
    &-content{
        top: 0;
        position: fixed;
        width: 35%;
        background-color: #fff;
        display: flex;
        flex-direction: column;
       justify-content: space-between;
       border-radius: 5px;
       box-shadow: 0 15px 35px rgba(50,50,93,.20);

      
       @include tablet{
           width: 40%;
          
       }
       @include mobile{
           width: 60%;
         
       }
        &-top{
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 20px;
            border-bottom: 1px solid rgba(0 ,0,0,.1);
            @include mobile{
                padding: 15px;
            }
            &-icon{
                color: rgba(0 ,0,0,.2);
                cursor: pointer;
            }
            &-icon:hover{
               color: rgba(0 ,0,0,.3);
            }
        }
        &-midtext{
            height: auto;
            padding: 20px;
            line-height: 1.5;
           @include mobile{
              
               padding: 15px;
           }
           
        }
        &-bottom{
            border-top: 1px solid rgba(0 ,0,0,.1);
            width: 100%;
            display: flex;
            padding: 20px;
            flex-direction: row-reverse;
            align-items: center;
            @include mobile{
                padding:15px;
            }
            &-btnS{
                width: 160px;
                height: 45px;
                background-color: #5e72e4;
                color: #fff;
                border: none;
                border-radius: 4px;
                box-shadow: 0 4px 6px rgba(50 ,50 ,93,.11);
                font-size: .875rem;
                text-transform: uppercase;
                cursor: pointer;
                font-weight: 500;
                @include mobile{
                    height: 35px;
                    width: 130px;
                }
            }
            &-btnS:hover{
                color: #fff;
                background-color: #3d55df;
                transform: translateY(-1px);
            }
            &-btnC{
                margin-right: 10px;
                width: 120px;
                height: 45px;
                background-color: #f4f5f7;
                color: #212529;
                border: none;
                border-radius: 4px;
                box-shadow: 0 4px 6px rgba(50 ,50 ,93,.11);
                font-size: .875rem;
                text-transform: uppercase;
                cursor: pointer;
                font-weight: 500;
                @include mobile{
                    height: 35px;
                    width: 100px;
                }
            }
            &-btnC:hover{
                color: #212529;
                background-color: #dee1e7;
                transform: translateY(-1px);
            }

        }
    }
}
.sizes-Small-containerN{
    transform: translateY(0px);
    // opacity: 1;
   
}

