@import "../../../App.scss";
.button{
        display: inline-block;
        color: black;
        font-size: 1em;
        padding: .625rem 1.25rem;
        border-radius: 3px;
        opacity: 0.9;
        border: 1px solid;
        text-align: center;
       //font-family:'Nunito' ,'Roboto', sans-serif;
        box-sizing: border-box;
        cursor: pointer;
        &:hover{
            opacity: 1;

        }
    &.primary {
        background-color: $--primary;
        color: white;
        border-color: $--primary;
        &:hover{
            transform: translateY(-1px);
            transition: .2s all ease-in-out;
            cursor: pointer;
            opacity: 1;
            box-shadow: 0 5px 10px rgba(50,50,93,0), 0 3px 6px rgba(0 ,0,0,.2);
        }
    
        &-rounded{
            border-radius: 6px;
            background-color: $--primary;
            color: white;
            border-color: $--primary;
            &:hover{
                transform: translateY(-1px);
                transition: .2s ease-in;
                cursor: pointer;
                opacity: 1;
                box-shadow: 0 5px 10px rgba(50,50,93,0), 0 3px 6px rgba(0 ,0,0,.2);
            }
        }
        &-outline{
    
            color: $--primary;
            background-color: transparent;
            border-color: $--primary;
            &:hover{
                background-color: $--primary;
                color: white;
                border-color: $--primary;
                transform: translateY(-1px);
                transition: .2s ease-in;
                cursor: pointer;
                opacity: 1;
                box-shadow: 0 5px 10px rgba(50,50,93,0), 0 3px 6px rgba(0 ,0,0,.2);
            }
    
            &-rounded{
                border-radius: 6px;
                color: $--primary;
                background-color: transparent;
                border-color: $--primary;
                &:hover{
                    background-color: $--primary;
                    color: white;
                    border-color: $--primary;
                    transform: translateY(-1px);
                    transition: .2s ease-in;
                    cursor: pointer;
                    opacity: 1;
                    box-shadow: 0 5px 10px rgba(50,50,93,0), 0 3px 6px rgba(0 ,0,0,.2);
                   }
    
            }
        }
    
        &-link
        {
            color: $--primary;
            background-color: transparent;
            border: none;
            cursor: pointer;
            padding: 0;
         
        }
    
    }
