@import "../../../App.scss";

.Social__Card{
     //display: inline-block;
    // color: black;
    border-radius: 3px;
     font-size: 2rem;
     font-weight: 600;
     //text-transform: uppercase;
    // padding: 0.25em 0.8em;
     border-radius: 3px;
     border: 1px solid;
    // text-align: center;
    // height: 40px;
    width: 220px; 
    @include flexColCenter;
    padding: 40px 30px;
    cursor: pointer;
    &:hover{
        transform: translateY(-2px);
        transition: .2s ease;
        opacity: 1;
    }
    .companyTag{
        color: white;
        font-size: 8px;
        font-weight: 300;
        // margin-top: 5px;

    }
    .icon{
       //padding-bottom: 40px;
       margin-bottom: 20px;
    }
    &.facebook{
      
        background-color: #3b5999;
        color: #fff;
        border-color: #3b5999;
        
    }
    &.twitter{
        background-color:  #1da1f2;
        color: #fff;
        border-color: #1da1f2;
        
    }
    &.instagram{
        background-color: #e4405f;
        color: #fff;
        border-color:#e4405f ;
    }
    &.git{
        background-color: #222;
        color: #fff;
        border-color:#222 ;
    }
    &.pintrest{
        background-color: #bd081c;
        color: #fff;
        border-color: #bd081c;
    }
    &.youtube{
        background-color: #cd201f;
        color: #fff;
        border-color:#cd201f ;
    }
    &.vimeo{
        background-color: #1ab7ea;
        color: #fff;
        border-color:#1ab7ea ;
    }
    &.slack{
        background-color: #3aaf85;
        color: #fff;
        border-color:#3aaf85 ;
    }
    &.dribble{
        background-color: #ea4c89;
        color: #fff;
        border-color:#ea4c89 ;
    }
    &.reddit{
        background-color: #ff4500;
        color: #fff;
        border-color:#ff4500 ;
    }
    &.tumblr{
        background-color: #35465c;
        color: #fff;
        border-color:#35465c ;
    }
    &.linkedin{
        background-color: #0077b5;
        color: #fff;
        border-color:#0077b5 ;
    }
      

}
.Social__button:hover{
    transform: translateY(-1px);
    transition: translateY 0.2s  ease-in-out;
    opacity: 1;
    cursor: pointer;
    box-shadow: 0 5px 10px rgba(50,50,93,0), 0 3px 6px rgba(0 ,0,0,.2);
}

