@import '../../components/abstracts/variables';
@import '../../components/abstracts/mixins';
.reset{
    display: flex;
    justify-content: center;
    width: 100%;
    background-color: #2d2d2d;
    //height: 100vh;
    padding: 50px 0px;
    align-items: center;
    &-card{
       max-width: 30%;
       width: 100%;
        border-radius: 3px;
        overflow: hidden;
       // box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.06);
       @include tablet{
           max-width: 50%;
           width: 100%;
       }
       @include mobile{
           max-width: 100%;
           width: 80%;
       }
        &-top{
            width: 100%;
            background-color:white;
            text-align: center;
            padding: 20px 20px ;
            line-height: 1.5;
           }
           &-bottom{
               padding: 40px;
               width: 100%;
               text-align: center;
               background-color:#f4f5f7;
               display: flex;
               flex-direction: column;
               align-items: center;
               &-input{
                   margin: 20px 0px 40px;
                   background-color: white;
                   display: flex;
                   box-shadow: -2px 4px 4px rgba(0, 8, 5, 0.1);
                   align-items: center;
                   padding: 15px;
                   color: #8898aa;
                   border-radius: 3px;
                
                   width: 100%;
                   input{
                       padding-left: 5px;
                       width:100%;
                       border-style:none;
                       outline: none;
                       font-size: 15px;
                       color: #8898aa;
                      
                   }  
               }
               Button{
                   width: fit-content;
               }
           }

    }
  
}